import React from "react"

const config = {
  default: "bg-n-200 text-n-800 border-transparent font-bold",
  primary: "bg-primary text-n-800 border-transparent",
  secondary: "bg-secondary text-n-100 border-transparent",
  white: "bg-n-100 text-n-800 border-n-300 font-bold",
  grey: "bg-n-300 text-n-800 border-transparent font-bold",
}

const Button = ({ type = "default", children, className, ...props }) => (
  <button
    className={`font-mono text-2xs py-3 px-7 whitespace-nowrap rounded-full border focus:outline-none focus:ring-4 ${
      config[type]
    } ${className || ""}`}
    {...props}
  >
    {children}
  </button>
)

export default Button
